<template>
    <div class="login-page-mojing">
        <div class="login-logo-mojing">
            <img :src="loginLogoImg" alt="" @click="changeBaseUrl">
        </div>
        <div class="tabDiv">密码登录</div>
        <div class="phone-number">
            <input
                    type="tel"
                    class="input-phone"
                    placeholder="输入手机号"
                    maxlength="11"
                    v-model="phoneNum"
                    onkeyup="value=value.replace(/[^\d]/g,'')"
                    @keydown.13="focusChange"
            />
        </div>
        <div style="height:0px; overflow: hidden;">
            <input
                    type="tel"
                    class="input-phone"
                    placeholder="输入手机号"
                    maxlength="11"
                    v-model="phoneNum"
                    onkeyup="value=value.replace(/[^\d]/g,'')"
                    @keydown.13="focusChange"
            />
        </div>
        <div class="phone-password">
            <input
                    type="password"
                    class="input-password"
                    placeholder="输入密码"
                    maxlength="16"
                    v-model="password"
                    ref="passwordFocus"
                    @keydown.13="getPubKey"
            />
        </div>
        <div class="forget-password" @click="forgetPwd()">
            忘记密码?点此重置
        </div>
        <!-- <div class="login-btn" :style="{'background': 'url('+ loginBtnBackground + ') center no-repeat'}" @click="clickedLoginBtn"> -->
        <!--    <div class="login-btn" :style="{'background-color': loginBtnBackground, 'box-shadow': loginBtnShadow}" @click="clickedLoginBtn">-->
        <div class="login-btn" :style="{'background-color': loginBtnBackground, 'box-shadow': loginBtnShadow}" @click="getPubKey">
            登录
        </div>

        <!-- 弹出框 -->
        <van-dialog
                v-model="isShowDialog"
                confirmButtonColor="#17B7BD"
                :message="dialogMessage"
                show-cancel-button
                :beforeClose="beforeClose"/>
        <reset-server ref="resetServer"/>
    </div>
</template>

<script>
    import config from '../../common/config'
    import {
        getPhoneValitService,
        requestLoginService,
        getRsaPublicKey,
        loginOwnerByJhk
    } from "../../services/loginService";

    import Vue from 'vue';
    import {Tab, Tabs, Dialog} from 'vant';
    import {storage} from '../../utils/storage'
    Vue.use(Tab).use(Tabs).use(Dialog);
    import {goWXShareDetail}  from '../../utils/skipLinkManage'


    // 登录logo
    import loginLogoImg from '@/assets/images/common/login_logo.png'
    //协议勾选图片
    import checkBoxImg from '@/assets/images/common/chebox_choice.png'
    //协议未勾选图片
    import uncheckBoxImg from '@/assets/images/common/check_default.png'
    //切换网络环境组件
    import ResetServer from '../Mine/ResetServer'
    export default {
        name: "MoJingLogin",
        components: {
            ResetServer
        },
        data() {
            return {
                verifyCodeStr: '获取验证码',
                verifyStrColor: '#17B7BD',
                phoneNum: '',
                phoneAuthCode: '',
                password: '',
                // 是否同意协议
                isAgreeprotocol: false,
                //登录按钮使能
                loginBtnEnable: false,
                //当前显示的tab
                // activeTab: 1,
                //是否显示提示框
                isShowDialog: false,
                //提示框内容
                dialogMessage: '',
                // 0： 表示未注册 1：未设密码
                dialogType: 0,
                // 验证码倒计时计时器
                vCodeClock: {},

                //本地图片
                loginLogoImg: loginLogoImg,
                checkBoxImg: checkBoxImg,
                uncheckBoxImg: uncheckBoxImg,
                //点击logo切换环境 点击次数
                clickTimes:0,
                publicKey:'',
                rsaPassWord:'',
            };
        },
        mounted(){
            let protocolFlag = storage.getStorage('protocalCheckFlag')
            if (!protocolFlag.$s_code_$exp) {
                //有值
                this.isAgreeprotocol = protocolFlag;
            }
            // this.getPubKey();
        },
        activated() {
            // 检查用户是否登录
            // this.checkUserLogin();
        },
        computed:{
            activeTab: {
                get() {
                    return this.$store.state.login.activeTab;
                },
                set(newVal) {
                    this.$store.dispatch('changeActiveTabFun', newVal);
                }
            },
            //协议选择box图片
            protocolCheckImg: function() {
                return this.isAgreeprotocol ? checkBoxImg : uncheckBoxImg;
            },
            //登录按钮背景色
            loginBtnBackground: function() {
                // return this.loginBtnEnable ? '#17B7BD' : '#D0CDCE';
                return '#17B7BD';
            },
            //登录按钮阴影
            loginBtnShadow: function() {
                // return this.loginBtnEnable ? '0 0 15px 0 rgba(165, 197, 165, 0.39)' : ' 0 0 15px 0 #D0CDCE';
                return '0 0 15px 0 rgba(165, 197, 165, 0.39)';
            }
        },
        watch: {
            // 监听手机号输入
            phoneNum: function(phoneNum) {
                this.changngeLoginBtnStatus();
            },
            //监听验证码输入
            phoneAuthCode: function(authCode) {
                this.changngeLoginBtnStatus();
            },
            //监听密码输入
            password: function(pwd) {
                this.changngeLoginBtnStatus();
            },
            activeTab: function(index) {
                // console.log('watch aactiveTab ' + index);
                this.changngeLoginBtnStatus();
            }
        },
        methods: {
            focusChange: function() {
                let isAuthcode = this.activeTab == 0 ? true : false;
                if (isAuthcode) {
                    this.$refs.authcodeFocus.focus();
                }
                else {
                    this.$refs.passwordFocus.focus();
                }
            },
            //修改登录按钮状态
            changngeLoginBtnStatus() {
                console.log('activeTab:' + this.activeTab);
                if (this.activeTab === 0) {
                    this.loginBtnEnable = this.phoneNum.length >= 11 && this.phoneAuthCode.length >= 6;
                }
                else {
                    this.loginBtnEnable = this.phoneNum.length >= 11 && this.password.length >= 6;
                }
            },
            // 获取验证码
            getPhoneValit: function() {
                if (this.verifyCodeStr != '获取验证码') {
                    return;
                }
                let phoneNumreg = /^1[0-9]{10}$/;
                if (this.phoneNum === undefined || this.phoneNum.length == 0) {
                    this.showMsg("请输入手机号");
                    return;
                }else if (!phoneNumreg.test(this.phoneNum)) {
                    this.showMsg("请输入有效的手机号码");
                    return;
                }
                getRsaPublicKey().then(res => {
                    this.publicKey = res.data.publicKey;
                    let rsaInfo = this.phoneNum+',14';//慧享家第三方来源固定14
                    let rsaPassWord = Vue.prototype.$getRSASign(rsaInfo,this.publicKey);
                    // 验证码倒计时
                    this.startVerifyCode();
                    // 请求验证码
                    getPhoneValitService(rsaPassWord)
                        .then(noteData => {
                            if (noteData.success == false) {
                                //失败
                                this.showMsg(noteData.msg);
                                this.removeVerifyCode();
                            }else {
                                //成功
                                this.showMsg('验证码已发送');
                            }
                        })
                        .catch(error => {
                            this.showMsg(error);
                            this.removeVerifyCode();
                        });
                }).catch(error => {
                    this.isLoading =false;
                    this.showMsg("网络异常，请稍后重试！");
                });
            },
            //启动验证码倒计时
            startVerifyCode: function() {
                //修改颜色
                this.verifyStrColor = '#bdbdbd';
                let totalTime = 60;
                this.verifyCodeStr = totalTime + "秒";
                this.vCodeClock = window.setInterval(() => {
                    totalTime--;
                    if (totalTime > 0) {
                        this.verifyCodeStr = totalTime + "秒";
                    }
                    else {
                        this.removeVerifyCode();
                    }
                }, 1000);
            },
            //移除验证码倒计时
            removeVerifyCode: function() {
                window.clearInterval(this.vCodeClock);
                //修改颜色
                this.verifyStrColor = '#17B7BD';
                this.verifyCodeStr = "获取验证码";
            },
            //点击协议
            clickedProtocol: function(index) {
                //1：信我家协议 2：隐私保护协议
                let type = index == 1 ? 1 : 3;
                this.$router.push({
                    name: "readTextPage",
                    params: {pageType: type}
                });
            },
            // 登录提示弹框回调
            beforeClose: function(action, done) {
                if (action === "confirm") {
                    if (this.dialogType == 0) {
                        //跳转验证码登录
                        this.activeTab = 0;
                    }
                    else if (this.dialogType == 1) {
                        //跳转设置密码
                        console.log('跳转设置密码');
                        this.$router.push({
                            name: "resetPwd",
                            params: {phoneNum: this.phoneNum}
                        });
                    }
                }
                done();
            },
            //点击忘记密码
            forgetPwd: function() {
                this.$router.push({
                    name: "resetPwd",
                    params: {phoneNum: this.phoneNum}
                });
            },
            //清空页面输入框
            clearPageInput() {
                this.phoneNum = '';
                this.phoneAuthCode = '';
                this.password = '';
            },
            // 判断是否可登录
            judgeLogin: function() {
                // if (!this.loginBtnEnable) {
                //     return false;
                // }
                // let isAuthcode = this.activeTab == 0 ? true : false;
                // if(isAuthcode && !this.isAgreeprotocol){
                //     this.showMsg('请勾选海信服务协议', 'center');
                //     return false;
                // }

                var myreg = /^1[0-9]{10}$/;
                if (!myreg.test(this.phoneNum)){
                    this.showMsg('请输入有效的手机号码', 'center');
                    return false;
                }
                return true;
            },
            //点击登录按钮
            clickedLoginBtn: function() {
                if (!this.judgeLogin()) {
                    return;
                }

                let reqData = {};
                    //密码
                    let basePassWord =  btoa(this.password)
                    let rsaInfo = this.phoneNum+','+basePassWord;
                    this.rsaPassWord = Vue.prototype.$getRSASign(rsaInfo,this.publicKey);
                    reqData = {
                        loginInfo :this.rsaPassWord
                    }

                requestLoginService(reqData, 1)
                    .then(noteData => {
                        if (noteData.resultCode == '0') {
                            //成功
                            if (noteData.data && noteData.data != undefined) {
                                this.loginSuccessDispose(noteData);//短信验证码和账号密码登录区分传参
                                storage.setStorage("accessToken",noteData.data.accessToken);
                                storage.setStorage("refreshToken",noteData.data.refreshToken,30);
                            } else {
                                //登录失败
                                showMsg("登录失败", 'center');
                            }
                        }
                        else {
                            //失败
                            this.loginFailDispose(noteData);
                        }
                    })
                    .catch(error => {
                        this.showMsg(error);
                    });
            },
            //登录失败
            loginFailDispose: function(respData) {
                if (respData.msg == '您还尚未设置密码，是否前往设置？') {
                    this.dialogType = 1;
                    this.dialogMessage = respData.msg;
                    this.isShowDialog = true;
                }
                else if (respData.msg == '用户账号不存在！') {
                    this.dialogType = 0;
                    this.dialogMessage = '该手机号尚未注册，请通过验证码登录';
                    this.isShowDialog = true;
                }
                else {
                    this.showMsg(respData.msg);
                }
            },
            //登录成功数据处理
            loginSuccessDispose: function(respData) {

                //登录成功标识，用来解决重新登录首页不刷新问题
                Vue.prototype.$refreshHomePage = true;
                //重新登录后，需要重新上报手机型号等基础信息
                Vue.prototype.$needUpOwner = true;
                //使用验证码成功登录之后，以后服务协议默认勾选
                if (storage.getStorage('protocalCheckFlag').$s_code_$exp) {
                    storage.setStorage("protocalCheckFlag", true, 999);
                }
                let userInfo = {};
                userInfo.isGygFlag = 0;//1是逛一逛标记 0不是  逛一逛不要查房间
                userInfo.OWNER_CODE = respData.data.ownerCode;
                userInfo.OWNER_NAME = respData.data.ownerName;
                userInfo.OWNER_NICK_NAME = respData.data.ownerNickName;
                userInfo.PHONE = respData.data.phone;
                // userInfo.PIC_BG_URL = respData.picBgUrl;
                userInfo.PIC_URL = respData.data.picUrl;
                userInfo.ACCESS_TOKEN=respData.data.accessToken;
                //小区信息
                userInfo.isNoArea = 0;//不是逛一逛
                //判断是否手动设置用户信息为虚拟小区
                let areaInfo={};
                if(this.$setVirtualArea){
                    areaInfo = this.$setUserAreaInfo;
                }else{
                    areaInfo = respData.data.areaInfo[0];
                }
                userInfo.AREA_CODE = areaInfo.areaCode;
                userInfo.AREA_NAME = areaInfo.areaName;
                userInfo.BUILD_CODE = areaInfo.buildCode;
                userInfo.BUILD_NAME = areaInfo.buildName;
                userInfo.CITY_CODE = areaInfo.cityCode;
                userInfo.CITY_NAME = areaInfo.cityName;
                userInfo.DEPT_CODE = areaInfo.deptCode;
                userInfo.FLOOR_NO = areaInfo.floorNo;
                userInfo.ROOM_CODE = areaInfo.roomCode;
                userInfo.ROOM_NAME = areaInfo.roomName;
                userInfo.UNIT_NO = areaInfo.unitNo;
                userInfo.AREA_NO = areaInfo.areaNo;//小区号
                userInfo.ROOM_NO = areaInfo.roomNo;//住家号
                //更新登录信息
                Vue.prototype.$setUserInfo(userInfo);
                Vue.prototype.$isLogin = true;
                //将注册登录信息回传app
                let paramsData={};
                paramsData.data=respData;
                paramsData=JSON.stringify(paramsData);
                let pid=11;
                if(this.$devicePlatform =="ios"){
                    //ios调原生
                    window.webkit.messageHandlers.JSCtrl.postMessage(['send_userInfo_xinwojia', pid,paramsData])
                }else if(this.$devicePlatform =="android"){
                    //android调原生
                    window.store.send_userInfo_xinwojia(pid,paramsData);
                }
                //检查是否需要微信分享跳转
                //替换成首页
                // this.$router.replace({ path: "/productPage" });
                this.clearPageInput();
                // } else {
                //   userInfo.isNoArea = 1;
                //   //更新登录信息
                //   Vue.prototype.$setUserInfo(userInfo);
                //   //检查是否需要微信分享跳转
                //   this.checkWXShare();
                //   // 跳转到房间绑定页面
                //   this.$router.push('/selectArea');
                //   this.clearPageInput();

                // var obj = {};
                // obj["flag"] = 2;//去绑房间1.逛一逛2.登录无绑定房间3.登录有绑定房间
                // $state.go('houseBindings', { object: JSON.stringify(obj) });
                // }
            },

            checkWXShare:function(){
                //获取微信分享跳转
                let data = storage.getStorage("WXShareData");
                console.log("checkWXShare = ");
                if (!data.$s_code_$exp) {
                    //删除微信分享跳转
                    storage.deleteStorage("WXShareData");
                    var that = this;
                    setTimeout(function () {
                        console.log("checkWXSharesetTimeout = ");
                        goWXShareDetail(that,data);
                    }, 200);
                }
            },
            //生成随机数
            random: function () {
                var myDate = new Date();//获取系统当前时间
                var year = myDate.getFullYear().toString();
                var month = myDate.getMonth().toString();
                var date = myDate.getDate().toString();
                var hours = myDate.getHours().toString();
                var minutes = myDate.getMinutes().toString();
                var seconds = myDate.getSeconds().toString();
                var randomNumber = year + month + date + hours + minutes + seconds + Math.floor(Math.random() * 1000).toString();
                return randomNumber;
            },
            //检查用户是否登录
            checkUserLogin: function() {
                //暂时不进入首页
                return;
                let obj = storage.getStorage("userInfo");
                if (null !== obj && "数据不存在" !== obj.message) {
                    //取数据到内存
                    this.$router.replace({ path: "/homePage" });
                }
            },
            showMsg: function(msg){
                this.$toast(msg);
            },
            changeBaseUrl(){
                // console.log(this.clickTimes)
                // if (!(config.version.includes('RC') || config.version.includes('IT') || config.version.includes('DEV'))){
                //   console.log('appVersion 不包含 RC 或 DEV 或 IT')
                //   return
                // }
                this.clickTimes++;
                if (this.clickTimes <= 11) return;
                this.clickTimes = 0;
                Dialog.confirm({
                    title: "是否需要修改网络环境"
                })
                    .then(() => {
                        this.$refs.resetServer.showServers = true;
                        this.removeVerifyCode()
                    })
                    .catch(() => {
                        // on cancel
                    });
            },
            getPubKey(){
                if (!this.judgeLogin()) {
                    return;
                }

                getRsaPublicKey().then(res => {
                    this.publicKey = res.data.publicKey;
                    this.clickedLoginBtn();
                }).catch(error => {
                    console.log(error);
                    this.isLoading =false;
                    this.showMsg("网络异常，请稍后重试！");
                });
            },

        },
    }
</script>

<style scoped lang="scss">
    $inputDivHeight: 27px;
    .login-page-mojing {
        height: 100%;
        width: 100%;
        background-color: #fff;
        overflow: auto;
    }

    .login-page-mojing .login-logo-mojing {
        text-align: center;
        padding-top: 20px;
        img {
            width: 20px;
            height: 20px;
        }
    }
    input {
        border:0;
        outline:none;
        background-color: transparent;
        caret-color:#17B7BD;
    }
    input::-webkit-input-placeholder {
        /* placeholder颜色  */
        color: #9A9A9A;
        /* placeholder字体大小  */
        font-size: 7px;
        font-family: PingFangSC-Medium;
        /* placeholder位置  */
        text-align: left;
    }

    .login-page-mojing .van-tabs {
        padding-top: 5%;
    }

    //设置bottomLine样式
    .van-hairline--top-bottom {
        height: 40px !important;
        line-height: 40px !important;
        border: 0 !important;
    }
    //设置tab居中显示
    /deep/.van-tabs__nav {
        padding-right: 76px;
        padding-left: 76px;
    }
    //tab标题字体设置
    /deep/.van-ellipsis {
        font-size: 9px;
    }

    /deep/.van-tab--active .van-ellipsis {
        font-family:PingFangSC-Medium;
    }

    /deep/.van-tab  {
        font-family:PingFangSC-Regular;
    }

    .phone-number,
    .phone-authcode,
    .phone-password {
        box-sizing: border-box;
        background: #F8F8F8;
        border-radius: 27px;
        margin: 10px 40px;
        height: $inputDivHeight;
        display: flex;
        align-items: center;
    }
    .phone-number {
        margin-top: 10px;
    }
    .input-phone,
    .input-authcode,
    .input-password {
        padding: 0px 32px;
        font-family: PingFangSC-Regular;
        font-size: 7px;
        height: 16px;
        line-height: 16px;
        color: #333333;
    }
    .input-authcode {
        padding-right: 0px;
        width: 156px;
    }
    .get-verification {
        width: 70px;
        height: $inputDivHeight;
        line-height: $inputDivHeight;
        font-family: PingFangSC-Medium;
        font-size: 7px;
        color: #17B7BD;
        text-align: right;
        white-space: nowrap;
    }

    .protocol {
        display: flex;
        width: 100%;
        height: 26px;
        line-height: 26px;
        font-family: PingFangSC-Regular;
        font-size: 5px;
        padding:20px 0;
        justify-content: center;
        overflow: hidden;

        .custom-checkbox {
            img {
                width: 24px;
                height: 24px;
                padding-right: 4px;
            }
        }
        .agree-text {
            color: #b2b2b2;
            white-space: nowrap;
        }
        .protocol-text {
            color: #17B7BD;
            white-space: nowrap;
        }
    }

    .forget-password {
        width: 130px;
        color: #17B7BD;
        text-align: center;
        margin: 0 auto;
        margin-top: 5%;
        margin-bottom: 0;
        font-family: PingFangSC-Regular;
        font-size: 6px;
        text-decoration: underline;
    }

    .login-page-mojing .login-btn {
        font-family: PingFangSC-Medium;
        font-size: 8px;
        width:46%;
        height: 22px;
        line-height: 22px;
        text-align: center;
        color: #f2f2f2;
        margin: 5% 27% 0;
        background-size: 100% !important;
        overflow: hidden;
        border-radius: 22px;
    }

    .van-dialog {
        width: 250px;
        font-size: 16px;
    }
    /deep/.van-dialog__message {
        font-size: 8px;
        font-family: PingFangSC-Medium;
    }
    .tabDiv{
        font-size: 8px;
        width: 100%;
        text-align: center;
    }
</style>